import { Component, OnInit } from '@angular/core';
import { Http } from '@angular/http';

import { ArticleService } from '../../_services/article.service';
import { Article } from '../../_models';
import { ARTICLETYPES } from '../../_datas';

@Component({
  selector: 'app-article-list',
  templateUrl: './article-list.component.html',
  styleUrls: ['./article-list.component.css'],
  providers: [ArticleService]
})
export class ArticleListComponent implements OnInit {
  articles: Article[] = [];
  ARTICLETYPES;

  delete(article,index){
    this.articles.splice(index,1);
    this.http.get("http://localhost:8080/ComSystem"+"/admin/deleteArticle"+"?articleId="+article.articleId)
      .map(response => console.log("ArticleListComponent.delete:", response.json()) || response.json())
      .subscribe(() => alert("删除成功!"));
  }

  constructor(private articleService: ArticleService,
    private http: Http) {
    Object.assign(this, { ARTICLETYPES });
  }

  ngOnInit() {
    this.getArticles();
  }

  getArticles() {
    this.articleService.getArticles()
      .subscribe(articles => this.articles = articles);
  }
}



