import { Contest } from './contest';

// 课时预算
export class Hour {
  hoursid: number;//课时预算id
  comid: number;//竞赛
  type: string;//课时类别
  hours: number;//课时数
  remarks: string;//备注

  constructor(hoursid: number, comid: number, type: string, hours: number, remarks: string) {
    this.hoursid = hoursid;
    this.comid = comid;
    this.type = type;
    this.hours = hours;
    this.remarks = remarks;
  }

  static newInstance(hour: Hour): Hour{
    return new Hour(
      hour.hoursid,
      hour.comid,
      hour.type,
      hour.hours,
      hour.remarks
    );
  }
}



