import { Injectable } from '@angular/core';
import { Http, Headers, Response, RequestOptions, URLSearchParams } from '@angular/http';

@Injectable()
export class AdminService {
  private baseUrl: string = "http://localhost:8080/CompSystem";

  constructor(private http: Http) { }

  uploadFile(file){
    let headers = new Headers();
    headers.append('Accept', 'application/json');
    headers.append('enctype', 'multipart/form-data');
    let options = new RequestOptions({ headers: headers });
    let formData:FormData = new FormData();
    formData.append('file',file,"测试");
    return this.http.post(this.baseUrl + "/admin/uploadFile", formData, options)
      .map(response => console.log("AdminService.uploadFile:", response.json()) || response.json());
  }
}



