import { Component, OnInit } from '@angular/core';
import { Http } from '@angular/http';

@Component({
  selector: 'app-files',
  templateUrl: './files.component.html',
  styleUrls: ['./files.component.css']
})
export class FilesComponent implements OnInit {
  files;

  getFiles() {
    this.http.get("http://localhost:8080/CompSystem/admin/allFile")
      .map(response => console.log("FilesComponent.getFiles:", response.json().filePage.list) || response.json().filePage.list)
      .subscribe(response => this.files = response);
  }

  download(file) {
    var url = "http://localhost:8080/CompSystem/index/fileDownload" + "?fileId=" + file.fileId;
    window.open(url);
  }

  constructor(private http: Http) { }

  ngOnInit() {
    this.getFiles();
  }

}



