import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Params }   from '@angular/router';
import 'rxjs/add/operator/switchMap';

import { Contest } from '../../_models';
import { ContestService } from '../../_services';

@Component({
  selector: 'app-contest',
  templateUrl: './contest-detail.component.html',
  styleUrls: ['./contest-detail.component.css'],
  providers: [ContestService]
})
export class ContestDetailComponent implements OnInit {
  contest: Contest;

  constructor(private contestService: ContestService,
    private route: ActivatedRoute) { }

  ngOnInit() {
    this.route.params
    .switchMap((params: Params) => this.contestService.getContest(+params['id']))
    .subscribe(contest => this.contest = contest);
  }

}



