import { Department } from './department';

// 管理员
export class Admin {
  adminNo: string;//账号
  password: string;//密码
  adminName: string;//姓名
  department: Department;//所属系

  constructor(adminNo: string, password: string, adminName: string, department: Department) {
    this.adminNo = adminNo;
    this.password = password;
    this.adminName = adminName;
    this.department = department;
  }
}



