import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/toPromise';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';

import { Contest } from '../_models';
import { CONTESTS } from '../_datas';

@Injectable()
export class ContestService {
  private baseUrl: string = 'http://localhost:8080/CompSystem';

  constructor(private http: Http) { }

  create(decaration): Observable<boolean> {
    let headers = new Headers({ 'Content-Type': 'application/json; charset=UTF-8' });
    let options = new RequestOptions({ headers: headers });
    return this.http.post(this.baseUrl + "/teacher/addComp", JSON.stringify(decaration), options)
      .map(response => true)
      .catch(this.handleError);
  }

  getContests(): Observable<Contest[]> {
    return this.http.get(this.baseUrl + "/index/CompetionList")
      .map(response => console.log("contestService.getContests:", response.json().comp) || response.json().comp as Contest[])
      .catch(this.handleError);
  }

  getStudentContests(studentNo): Observable<Contest[]> {
    return this.http.get(this.baseUrl + "/student/getComp" + "?studentNo=" + studentNo)
      .map(response => console.log("contestService.getStudentContests:", response.json().stuCom) || response.json().stuCom as Contest[])
      .catch(this.handleError);
  }

  getCompGuideTeacher(comid) {
    return this.http.get(this.baseUrl + "/student/getCompGuideTeacher" + "?comid=" + comid)
      .map(response => console.log("contestService.getCompGuideTeacher:", response.json()) || response.json())
      .catch(this.handleError);
  }

  getContest(id: number): Observable<Contest> {
    return this.getContests()
      .map(contests => {
        var response = contests.find(contest => contest.comid === id);
        console.log("contestService.getContest:", response);
        return response;
      });
  }

  getDeclarations(id: number): Observable<any> {
    return this.http.get(this.baseUrl + "/teacher/getCompResult" + "?teacherNo=" + id)
      .map(response => console.log("contestService.getDeclarations:", response.json()) || response.json())
      .catch(this.handleError);
  }

  getGroups(id: number): Observable<any> {
    return this.http.get(this.baseUrl + "/student/getAllGroups" + "?comId=" + id)
      .map(response => console.log("contestService.getGroups:", response.json()) || response.json())
      .catch(this.handleError);
  }

  createGroup(group) {
    let headers = new Headers({ 'Content-Type': 'application/json; charset=UTF-8' });
    let options = new RequestOptions({ headers: headers });
    return this.http.post(this.baseUrl + "/student/createGroup", group, options)
      .catch(this.handleError);
  }

  getConditions() {
    return this.http.get(this.baseUrl + "/student/getAllConditions")
      .map(response => console.log("ContestService.getConditions:", response.json()) || response.json())
      .catch(this.handleError);
  }

  private handleError(error: Response | any) {
    // In a real world app, we might use a remote logging infrastructure
    let errMsg: string;
    if (error instanceof Response) {
      const body = error.json() || '';
      const err = body.error || JSON.stringify(body);
      errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
    } else {
      errMsg = error.message ? error.message : error.toString();
    }
    console.error(errMsg);
    return Promise.reject(errMsg);
  }
}



