import { Prize } from './prize';
import { Level } from './level';
import { Group } from './group';
import { Contest } from './contest';

// 获奖情况
export class Award {
  awardsId: number;//获奖情况id
  name: string;//竞赛名称 TODO 非必选,Contest->name
  date: string;//竞赛时间 TODO 非必选,Contest->date
  hunit: string;//主办单位 TODO 非必选,Contest->hunit
  prize: Prize;//获奖等级
  level: Level;//竞赛等级 TODO 非必选,Contest->level
  groups: Group[];//参赛组
  competition: Contest;//竞赛
  isPublish: number;//是否被发布

  AwardsId(awardsId: number): Award {
    this.awardsId = awardsId;
    return this;
  }
  Name(name: string): Award {
    this.name = name;
    return this;
  }
  Date(date: string): Award {
    this.date = date;
    return this;
  }
  HUnit(hunit: string): Award {
    this.hunit = hunit;
    return this;
  }
  Prize(prize: Prize): Award {
    this.prize = prize;
    return this;
  }
  Level(level: Level): Award {
    this.level = level;
    return this;
  }
  Groups(groups: Group[]): Award {
    this.groups = groups;
    return this;
  }
  Competition(competition: Contest): Award {
    this.competition = competition;
    return this;
  }
  IsPublish(isPublish: number): Award {
    this.isPublish = isPublish;
    return this;
  }
}



