import { Component, OnInit } from '@angular/core';

import { Student } from '../../../_models';
import { LoginService,ContestService } from '../../../_services';

@Component({
  selector: 'app-student-contest',
  templateUrl: './student-contest.component.html',
  styleUrls: ['./student-contest.component.css'],
  providers: [LoginService,ContestService]
})
export class StudentContestComponent implements OnInit {
  student;
  appliedContests;

  constructor(private loginService: LoginService,private contestService: ContestService) { }

  ngOnInit() {
    this.student = this.loginService.getUser();
    this.contestService.getStudentContests(this.student.studentno)
      .subscribe(contests => this.appliedContests = contests);
  }
}



