import { Component, OnInit, Input } from '@angular/core';

import { ContestService, LoginService } from '../../_services';

@Component({
  selector: 'app-declaration-list',
  templateUrl: './declaration-list.component.html',
  styleUrls: ['./declaration-list.component.css'],
  providers: [ContestService, LoginService]
})
export class DeclarationListComponent implements OnInit {
  @Input() user: any;
  private declarations: any[] = [];

  constructor(private contestService: ContestService,
    private loginService: LoginService) { }

  ngOnInit() {
    if (!this.user)
      this.user = this.loginService.getUser();
    this.getDeclarations();
  }

  getDeclarations() {
    this.contestService.getDeclarations(this.user.teacherno)
      .subscribe(declarations => this.declarations = declarations);
  }
}



