import { Component, OnInit, ElementRef, Input, ViewChild } from '@angular/core';
import { MdDialog, MdDialogRef } from '@angular/material/dialog';
import { NgForm, FormControl } from '@angular/forms';
import { Http } from '@angular/http';

import { AwardService } from '../../../_services';
@Component({
  selector: 'app-admin-works',
  templateUrl: './admin-works.component.html',
  styleUrls: ['./admin-works.component.css']
})
export class AdminWorksComponent implements OnInit {
  private dialogRef: MdDialogRef<WorksDialog>;
  works;
  getWorks(){
    this.http.get("http://localhost:8080/CompSystem/admin/productionList")
      .map(response => console.log("WorksDialog.getWorks:", response.json().produPageInfo.list) || response.json().produPageInfo.list)
      .subscribe(response => this.works = response);
  }
  delete(work,index){
    this.http.get("http://localhost:8080/CompSystem/admin/deleteProduction" + "?proId="+ work.proId)
      .map(response => console.log("AdminWorksComponent.delete:", response.json()) || response.json())
      .subscribe(() => {
        this.works.splice(index,1);
        alert("删除成功!");
      });
  }
  constructor(public dialog: MdDialog, private http: Http) { }

  ngOnInit() {
    this.getWorks();
  }

  openDialog() {
    this.dialogRef = this.dialog.open(WorksDialog, {
      disableClose: false
    });

    this.dialogRef.afterClosed().subscribe(result => {
      if (result) {
      }
      this.dialogRef = null;
    });
  }
}

@Component({
  selector: 'app-works-dialog',
  templateUrl: './works.dialog.html',
  styleUrls: ['./works.dialog.css'],
  providers: [AwardService]
})
export class WorksDialog implements OnInit {
  @ViewChild('fileInput') inputEl: ElementRef;
  apply(work){
    let inputEl: HTMLInputElement = this.inputEl.nativeElement;
    let fileCount: number = inputEl.files.length;
    let formData = new FormData();
    if (fileCount > 0) { // a file was selected
      formData.append('file', inputEl.files.item(0));
      formData.append('proName', work.proName);
      formData.append('award', work.award);
      formData.append('membersName', work.membersName);
      formData.append('introduction', work.introduction);
      this.http.post("http://localhost:8080/CompSystem/admin/uploadProduction", formData)
        .map(response => console.log("WorksDialog.apply:", response.json()) || response.json())
        .subscribe(response => console.log(response) || alert("提交成功!"));
    }
  }
  constructor(public dialogRef: MdDialogRef<WorksDialog>,
  private awardService: AwardService,
  private http: Http) { }
  ngOnInit() {
  }
}



