import { Component, OnInit, ElementRef, Input, ViewChild } from '@angular/core';
import { MdDialog, MdDialogRef } from '@angular/material/dialog';
import { Http } from '@angular/http';

import { Student } from '../../../_models';
import { LoginService, StudentService, ContestService } from '../../../_services';

@Component({
  selector: 'app-student-info',
  templateUrl: './student-info.component.html',
  styleUrls: ['./student-info.component.css'],
  providers: [StudentService]
})
export class StudentInfoComponent implements OnInit {
  private dialogRef: MdDialogRef<MaterialDialog>;
  student: Student;
  materials;
  studentEditCopy: Student;
  studentInfo = {
    sno: "学号",
    name: "姓名",
    gender: "性别",
    department: "学院(系)",
    profession: "专业",
    classno: "班别",
    grade: "年级",
    phone: "手机号",
    email: "邮箱",
  }

  disableEdit: boolean = true;

  getMaterials() {
    this.studentService.getMaterials(this.student.studentno)
      .subscribe(materials => this.materials = materials);
  }

  constructor(private loginService: LoginService, private studentService: StudentService, public dialog: MdDialog) { }

  ngOnInit() {
    this.student = this.loginService.getUser();
    this.getMaterials();
  }

  toggleEdit() {
    this.disableEdit = !this.disableEdit;
  }

  edit() {
    this.studentEditCopy = JSON.parse(JSON.stringify(this.student));
  }

  save() {
    console.log("student-info.save:", this.student);
    this.studentService.update(this.student).subscribe(response => {
      if (!response)
        this.student = this.studentEditCopy;
    });
  }

  cancel() {
    this.student = this.studentEditCopy;
  }

  openDialog() {
    this.dialogRef = this.dialog.open(MaterialDialog, {
      disableClose: false
    });

    this.dialogRef.afterClosed().subscribe(result => {
      if (result) {
      }
      this.dialogRef = null;
    });
  }
}

@Component({
  selector: 'app-material-dialog',
  templateUrl: './material.dialog.html',
  styleUrls: ['./material.dialog.css'],
  providers: [ContestService, StudentService, LoginService]
})
export class MaterialDialog implements OnInit {
  @Input() multiple: boolean = false;
  @ViewChild('fileInput') inputEl: ElementRef;
  conditions;
  student;

  constructor(public dialogRef: MdDialogRef<MaterialDialog>,
    private contestService: ContestService,
    private studentService: StudentService,
    private loginService: LoginService,
    private http: Http) { }

  ngOnInit() {
    this.student = this.loginService.getUser();
    this.getConditions();
  }

  getConditions() {
    this.contestService.getConditions().subscribe(conditions => this.conditions = conditions);
  }

  saveMaterial(material) {
    let inputEl: HTMLInputElement = this.inputEl.nativeElement;
    let fileCount: number = inputEl.files.length;
    let formData = new FormData();
    if (fileCount > 0) { // a file was selected
      formData.append('file', inputEl.files.item(0));
      formData.append('conditionid', material.condition);
      formData.append('studentno', this.student.studentno);
      this.http.post("http://localhost:8080/CompSystem/student/saveMaterial", formData)
        .subscribe(() => alert('上传成功!'));
      // this.studentService.saveMaterial(file.file).subscribe(response => console.log(response));
    }
  }
}



