import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/toPromise';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';

import { Teacher } from '../_models';

@Injectable()
export class TeacherService {
  private teacherUrl: string = 'http://localhost:8080/CompSystem/teacher';

  constructor(private http: Http) { }

  select(teacherNo: number): Observable<Teacher> {
    return this.http.get(this.teacherUrl + "/getTeacherInfo" + "?teacherNo=" + teacherNo)
      .map(response => console.log(response.json()) || response.json() as Teacher)
      .catch(this.handleError);
  }

  update(teacher: Teacher): Observable<any> {
    let headers = new Headers({ 'Content-Type': 'application/json; charset=UTF-8' });
    let options = new RequestOptions({ headers: headers });
    return this.http.post(this.teacherUrl + "/updateTeacherInfo", teacher, options)
      .map(response => {
        if (response.json())
          localStorage.setItem('currentUser', JSON.stringify(teacher))
        return console.log("teacherService.update", response.json()) || response.json();
      })
      .catch(this.handleError);
  }

  private handleError(error: Response | any) {
    // In a real world app, we might use a remote logging infrastructure
    let errMsg: string;
    if (error instanceof Response) {
      const body = error.json() || '';
      const err = body.error || JSON.stringify(body);
      errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
    } else {
      errMsg = error.message ? error.message : error.toString();
    }
    console.error(errMsg);
    return Promise.reject(errMsg);
  }
}



