import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions, RequestMethod } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/toPromise';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';

import { Student } from '../_models';

@Injectable()
export class StudentService {
  private baseUrl: string = 'http://localhost:8080/CompSystem';
  private studentUrl: string = 'http://localhost:8080/CompSystem/student';

  constructor(private http: Http) {
  }

  update(student: Student): Observable<any> {
    let headers = new Headers({ 'Content-Type': 'application/json; charset=UTF-8' });
    let options = new RequestOptions({ headers: headers });
    return this.http.post(this.studentUrl + "/updateInfo", student, options)
      .map(response => {
        if (response.json())
          localStorage.setItem('currentUser', JSON.stringify(student))
        return console.log("studentService.update:", response.json()) || response.json();
      })
      .catch(this.handleError);
  }

  getMaterials(studentNo) {
    return this.http.get(this.baseUrl + "/student/getMaterial" + "?studentNo=" + studentNo)
      .map(response => console.log("studentService.getMaterials:", response.json()) || response.json())
      .catch(this.handleError);
  }

  saveMaterial(file: File) {
        console.log("file:",file);
    let headers = new Headers();
    headers.append('Accept', 'application/json');
    headers.append('enctype', 'multipart/form-data');
    let options = new RequestOptions({ headers: headers });
    let formData:FormData = new FormData();
    formData.append('file',file,"测试");
    // formData.append('material',null);
    return this.http.post(this.studentUrl + "/saveMaterial", formData, options)
      .map(response => true)
      .catch(this.handleError);
  }

  private handleError(error: Response | any) {
    // In a real world app, we might use a remote logging infrastructure
    let errMsg: string;
    if (error instanceof Response) {
      const body = error.json() || '';
      const err = body.error || JSON.stringify(body);
      errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
    } else {
      errMsg = error.message ? error.message : error.toString();
    }
    console.error(errMsg);
    return Promise.reject(errMsg);
  }
}



