import { Pipe, PipeTransform } from '@angular/core';

import { Article, ArticleType } from '../_models';

@Pipe({
  name: 'articleTypeFilter',
  pure: false
})
export class ArticleTypeFilterPipe implements PipeTransform {

  transform(articles: Article[], articleType: ArticleType): Article[] {
    return articles.filter(article => article.articleType === articleType.id);
  }

}



