import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { HashLocationStrategy, LocationStrategy } from '@angular/common';
import { MaterialModule } from '@angular/material';
import { FlexLayoutModule } from '@angular/flex-layout';

import { NgxChartsModule } from '@swimlane/ngx-charts';
import { QuillEditorModule } from 'ng2-quill-editor';
import { QRCodeModule } from 'angular2-qrcode';

import { routing }        from './app.routing';
import { AuthGuard } from './guard';

import { AppComponent } from './app.component';
import { HomeComponent } from './home';
import { LoginComponent, LoginDialog } from './login';
import { UserComponent,
  AdminHomeComponent, TeacherInfoComponent, StudentInfoComponent,
  AdminContestComponent, StudentContestComponent, MaterialDialog, WorksDialog
} from './user';
import { ArticleComponent, ArticleListComponent, ArticleEditComponent,
  ArticleDetailComponent, ApplyDialog
} from './article';
import { ContestComponent, ContestListComponent, ContestDetailComponent
} from './contest';
import { ArticleTypeFilterPipe, ContestUserFilterPipe, ContestTimelineFilterPipe
} from './_pipes';
import { DeclarationComponent
} from './declaration';
import { AwardComponent, AwardListComponent } from './award';
import { ChartComponent } from './chart';
import { UserInfoComponent } from './user/user-info/user-info.component';
import { ExaminerComponent } from './declaration/examiner/examiner.component';
import { DeclarationListComponent } from './declaration/declaration-list/declaration-list.component';
import { WorksComponent } from './works/works.component';
import { NotificationComponent } from './notification/notification.component';
import { TeacherContestComponent } from './user/teacher/teacher-contest/teacher-contest.component';
import { FilesComponent } from './files/files.component';
import { AdminWorksComponent } from './user/admin/admin-works/admin-works.component';
import { AdminFilesComponent } from './user/admin/admin-files/admin-files.component';
import { StudentMessageComponent } from './user/student/student-message/student-message.component';
import { TeacherMessageComponent } from './user/teacher/teacher-message/teacher-message.component';
import { AdminMaterialComponent } from './user/admin/admin-material/admin-material.component';

@NgModule({
  declarations: [
    AppComponent, HomeComponent,
    LoginComponent, LoginDialog,
    UserComponent,MaterialDialog, WorksDialog,
    AdminHomeComponent, TeacherInfoComponent, StudentInfoComponent,
    AdminContestComponent, StudentContestComponent,
    ArticleComponent, ArticleListComponent, ArticleEditComponent, ArticleDetailComponent, ApplyDialog,
    ContestComponent, ContestListComponent, ContestDetailComponent,
    ArticleTypeFilterPipe, ContestUserFilterPipe, ContestTimelineFilterPipe,
    DeclarationComponent,
    AwardComponent, AwardListComponent,
    ChartComponent,
    UserInfoComponent,
    ExaminerComponent,
    DeclarationListComponent,
    WorksComponent,
    NotificationComponent,
    TeacherContestComponent,
    FilesComponent,
    AdminWorksComponent,
    AdminFilesComponent,
    StudentMessageComponent,
    TeacherMessageComponent,
    AdminMaterialComponent
  ],
  entryComponents: [
    LoginDialog, ApplyDialog,MaterialDialog,WorksDialog
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    MaterialModule,
    FlexLayoutModule,
    NgxChartsModule,
    QuillEditorModule,
    QRCodeModule,
    routing
  ],
  providers: [
    AuthGuard,
    {provide: LocationStrategy, useClass: HashLocationStrategy}
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



