import { Component, OnInit } from '@angular/core';

import { Http } from '@angular/http';
import { LoginService } from '../../../_services';

@Component({
  selector: 'app-teacher-message',
  templateUrl: './teacher-message.component.html',
  styleUrls: ['./teacher-message.component.css'],
  providers: [LoginService]
})
export class TeacherMessageComponent implements OnInit {
  teacher;
  messages;

  getMessages(){
    this.http.get("http://localhost:8080/CompSystem/teacher/getAllMessage" + "?no=" + this.teacher.teacherno)
      .map(response => console.log("TeacherMessageComponent.getMessages:", response.json()) || response.json())
      .subscribe(response => this.messages = response);
  }

  constructor(private http: Http,private loginService: LoginService) { }

  ngOnInit() {
    this.teacher = this.loginService.getUser();
  }

}



