import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { MdDialog, MdDialogRef } from '@angular/material/dialog';
import { MdMenuTrigger } from '@angular/material/menu';

import { LoginService } from '../_services';
import { Login, Role } from '../_models';

import { Http } from '@angular/http';
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css'],
  providers: [LoginService]
})

export class LoginComponent implements OnInit {
  private dialogRef: MdDialogRef<LoginDialog>;
  private user: any;
  private role: any;

  constructor(public dialog: MdDialog, private loginService: LoginService,
    private router: Router, private http: Http) { }

  openDialog() {
    this.dialogRef = this.dialog.open(LoginDialog, {
      disableClose: false
    });

    this.dialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.login(result);
      }
      this.dialogRef = null;
    });
  }

  login(form: Login) {
    this.loginService.login(form)
      .subscribe(response => {
        if(response){
          this.user = response;
          this.role = form.role;
        }
      });
  }

  logout() {
    this.loginService.logout();
    this.user = null;
    this.router.navigate(['']);
  }

  ngOnInit() {
    this.user = this.loginService.getUser();
    this.role = this.loginService.getRole();
    this.loginService.componentMethodCalled$
      .subscribe(() => {console.log("try to openDialog!!");this.openDialog()});
  }
}

@Component({
  selector: 'app-login-dialog',
  templateUrl: './login.dialog.html',
  styleUrls: ['./login.dialog.css'],
  providers: [LoginService]
})

export class LoginDialog {
  roles: Role[];
  errorMessage: any;

  constructor(public dialogRef: MdDialogRef<LoginDialog>,
    private loginService: LoginService) { }

  ngOnInit() {
    this.loginService.logout();
    this.getRoles();
  }

  getRoles() {
    this.loginService.getRoles()
      .then(
      roles => this.roles = roles,
      error => this.errorMessage = <any>error);
  }
}



