import { Admin } from './admin';
import { Project } from './project';

// 文章
export class Article {
  articleId: number;//文章id
  title: string;//标题
  content: string;//内容
  pubDate: string;//发布日期
  adminName: string;//管理员名
  pubUserNo: number;//发布者
  articleType: number;//发布的类型 1--征文,2--动态,3--获奖
  comId: number;//竞赛id

  constructor(articleId: number, title: string, content: string, pubDate: string,
    adminName: string, pubUserNo: number, articleType: number, comId: number) {
      this.articleId = articleId;
      this.title = title;
      this.content = content;
      this.pubDate = pubDate;
      this.adminName = adminName;
      this.pubUserNo = pubUserNo;
      this.articleType = articleType;
      this.comId = comId;
  }
}



