import { GroupDetail } from './group-detail';

// 学生账号信息
export class Student {
  studentno: number;//学号
  studentname: string;//姓名
  gender: string;//性别
  department: string;//学院(系)
  profession: string;//专业
  classno: number;//班别 TODO classNo
  grade: number;//年级
  phone: string;//手机号 TODO number
  email: string;//邮箱
  password: string;//密码
  groupsDetails: GroupDetail[];//组详情 TODO

  constructor(studentno: number, studentname: string, gender: string,
    department: string, profession: string, classno: number, grade: number,
    phone: string, email: string, password: string, groupsDetails: GroupDetail[]) {
    this.studentno = studentno;
    this.studentname = studentname;
    this.gender = gender;
    this.department = department;
    this.profession = profession;
    this.classno = classno;
    this.grade = grade;
    this.phone = phone;
    this.email = email;
    this.password = password;
    this.groupsDetails = groupsDetails;
  }
}



