import { Teacher } from './teacher';
import { Level } from './level';
import { Department } from './department';
import { Budget } from './budget';
import { Hour } from './hour';
import { Schedule } from './schedule';
import { Group } from './group';

// 申报表
export class Contest {
  comid: number;//竞赛id
  comname: string;//竞赛名称
  teacher: Teacher;//竞赛负责人
  unit: string;//所在单位(盖章)
  level: Level;//竞赛类别
  phone: string;//联系电话
  email: string;//电子邮箱
  department: Department;//系
  date: string;//填表日期
  host: string;//院级以上竞赛举办者
  time: string;//竞赛时间
  place: string;//竞赛地点
  introduce: string;//介绍 TODO 动词?
  object: string;//参赛对象
  people: number;//人数
  sponsor: string;//校外资助单位
  plan: string;//学科竞赛实施方案
  tgoal: string;//能力训练目标 TODO tGoal
  wgoal: string;//竞赛获奖目标 TODO wGoal
  sopinion: string;//校外资助单位意见 TODO sOpinion
  slsign: string;//资助单位负责人签字(盖章) TODO SLSign
  slsdate: string;//资助单位负责人签字日期 TODO SLSDate
  uopinion: string;//竞赛组织实施单位意见 TODO uOpinion
  dsign: string;//系部签字(盖章) TODO dSign
  dsdate: string;//系部签字日期 TODO dSDate
  tdopinion: string;//教学处意见 TODO tDOpinion
  olsign: string;//竞赛办公室负责人签字 TODO oLSign
  olsdate: string;//竞赛办公室负责人签字日期 TODO oLSDate
  lsign: string;//负责人签字(盖章) TODO lSign
  lsdate: string;//负责人签字日期 TODO lSDate
  status: number;//状态 (0等待系主任审批 1.等待教学处审批 2申报成功3申报失败)
  applyBeginDate: string;//报名开始时间
  applyEndDate: string;//报名结束时间
  comDate: string;//比赛时间
  // budget: Budget[];//经费预算
  // hour: Hour[];//课时预算
  // schedule: Schedule[];//培训安排
  // groups: Group[];//参赛组
  // guideTeachers: Teacher[];//指导教师
  constructor(comid: number, comname: string, teacher: Teacher, unit: string,
    level: Level, phone: string, email: string, department: Department,
    date: string, host: string, time: string, place: string, introduce: string,
    object: string, people: number, sponsor: string, plan: string,
    tgoal: string, wgoal: string, sopinion: string, slsign: string,
    slsdate: string, uopinion: string, dsign: string, dsdate: string,
    tdopinion: string, olsign: string, olsdate: string, lsign: string,
    lsdate: string, status: number,
    applyBeginDate: string, applyEndDate: string, comDate: string) {
    this.comid = comid;
    this.comname = comname;
    this.teacher = teacher;
    this.unit = unit;
    this.level = level;
    this.phone = phone;
    this.email = email;
    this.department = department;
    this.date = date;
    this.host = host;
    this.time = time;
    this.place = place;
    this.introduce = introduce;
    this.object = object;
    this.people = people;
    this.sponsor = sponsor;
    this.plan = plan;
    this.tgoal = tgoal;
    this.wgoal = wgoal;
    this.sopinion = sopinion;
    this.slsign = slsign;
    this.slsdate = slsdate;
    this.uopinion = uopinion;
    this.dsign = dsign;
    this.dsdate = dsdate;
    this.tdopinion = tdopinion;
    this.olsign = olsign;
    this.olsdate = olsdate;
    this.lsign = lsign;
    this.lsdate = lsdate;
    this.status = status;
    this.applyBeginDate = applyBeginDate;
    this.applyEndDate = applyEndDate;
    this.comDate = comDate;
  }
}



