import { Pipe, PipeTransform } from '@angular/core';

import { Contest } from '../_models';

@Pipe({
  name: 'contestTimelineFilter',
  pure: false
})
export class ContestTimelineFilterPipe implements PipeTransform {

  transform(contests: Contest[]): Contest[] {
    if (contests)
      return contests
        //排序 最接近今天 -> 最远离今天
        .sort((contest1, contest2) => {
          let currentDate =
            (d => new Date(d))(new Date().toLocaleDateString()).valueOf();
          var distanceA = currentDate - new Date(contest1.time).valueOf();
          var distanceB = currentDate - new Date(contest2.time).valueOf();
          // 如果一样接近，则以 之前优先于之后
          if (Math.abs(distanceA) - Math.abs(distanceB) == 0) {
            return distanceA < 0 ? distanceA : distanceB;
          }
          return Math.abs(distanceA) - Math.abs(distanceB);
        })
        //取前n条
        .slice(0, 6)
        //排序 时间顺序
        .sort((contest1, contest2) =>
          new Date(contest1.time).valueOf() - new Date(contest2.time).valueOf());
  }

}



