import { Contest } from './contest';

// 经费预算
export class Budget {
  budgetid: number;//经费预算id
  comid: number;//竞赛
  subject: string;//支出科目
  sum: number;//金额
  reasons: string;//计算依据及理由

  constructor(budgetid: number, comid: number, subject: string, sum: number, reasons: string){
    this.budgetid = budgetid;
    this.comid = comid;
    this.subject = subject;
    this.sum = sum;
    this.reasons = reasons;
  }

  static newInstance(budget: Budget): Budget{
    return new Budget(
      budget.budgetid,
      budget.comid,
      budget.subject,
      budget.sum,
      budget.reasons
    )
  }
}



