import { Injectable } from '@angular/core';
import { Http, Headers, Response, RequestOptions, URLSearchParams } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/toPromise';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import { Article, ArticleType } from '../_models';
import { ARTICLETYPES, ARTICLES } from '../_datas';

@Injectable()
export class ArticleService {
  private baseUrl: string = "http://localhost:8080/CompSystem";
  private articleTypesUrl: string = 'http://localhost:8080/CompSystem/getArticleTypes';
  private articlesUrl: string = this.baseUrl + "/index/articlesList";

  constructor(private http: Http) { }

  getArticleTypes(): Promise<ArticleType[]> {
    return Promise.resolve(ARTICLETYPES);
    // this.http.get(this.articleTypesUrl)
    //   .toPromise()
    //   .then(response => response.json() as ArticleType[])
    //   .catch(this.handleError);
  }

  getArticles(): Observable<Article[]> {
    return this.http.get(this.articlesUrl)
      .map(response => console.log("articleService.getArticles:",response.json().articlePageInfo.list) ||
        response.json().articlePageInfo.list as Article[])
      .catch(this.handleError);
  }

  pubArticle(article){
    let headers = new Headers({ 'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8' });
    let options = new RequestOptions({ headers: headers });
    let urlSearchParams = new URLSearchParams();
    urlSearchParams.append('title', article.title);
    urlSearchParams.append('content', article.content);
    urlSearchParams.append('articleType', article.articleType);
    urlSearchParams.append('relationId', article.relationId);
    console.log(urlSearchParams.toString());
    return this.http.post(this.baseUrl + "/admin/pubArticle", urlSearchParams, options)
      .map(response => console.log("ArticleService.pubArticle:", response.json()) || response.json())
      .catch(this.handleError);
  }

  getArticle(id: number): Observable<Article> {
    return this.getArticles()
      .map(articles => articles.find(article => article.articleId === id));
  }

  getArticleByComp(id: number): Observable<Article> {
    return this.getArticles()
      .map(articles => articles.find(article => article.comId === id));
  }

  private handleError(error: Response | any) {
    // In a real world app, we might use a remote logging infrastructure
    let errMsg: string;
    if (error instanceof Response) {
      const body = error.json() || '';
      const err = body.error || JSON.stringify(body);
      errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
    } else {
      errMsg = error.message ? error.message : error.toString();
    }
    console.error(errMsg);
    return Promise.reject(errMsg);
  }

}



