import { Component, OnInit } from '@angular/core';

import { LoginService } from '../_services';

@Component({
  selector: 'app-user',
  templateUrl: './user.component.html',
  styleUrls: ['./user.component.css'],
  providers: [LoginService]
})
export class UserComponent implements OnInit {
  private user: any;
  private role: any;
  links: Object[];

  constructor(private loginService: LoginService) { }

  ngOnInit() {
    this.user = this.loginService.getUser();
    this.role = this.loginService.getRole();
    if (this.user)
      switch (this.role) {
        case "admin": {
          this.links = [
            { href: '/admin', name: '仪表板' },
            { href: 'article', name: '文章模块' },
            { href: 'contest', name: '竞赛模块' },
            { href: 'works', name: '作品模块'},
            { href: 'files', name: '文件上传'}
          ];
        }
          break;
        case "teacher": {
          this.links = [
            { href: '/teacher', name: '个人信息' },
            { href: 'declaration', name: '申报竞赛' },
            { href: 'declarationList', name: '申报竞赛列表' },
            { href: 'feedbackAward', name: '反馈获奖'}
          ];
          if (this.user.examiner === 1) {
            this.links.push({ href: 'examiner', name: '审批竞赛' });
          }
        }
          break;
        case "student": {
          this.links = [
            { href: '/student', name: '个人信息' },
            { href: 'contest', name: '参加竞赛' },
            { href: 'award', name: '获奖情况' }
          ];
        }
          break;
        default: ;
      }
    if (this.role === "teacher" || this.role === "student") {
      this.links.push({ href: '/notification', name: '消息' });
    }
  }

}



