import { Pipe, PipeTransform } from '@angular/core';

import { Contest } from '../_models';

@Pipe({
  name: 'contestUserFilter',
  pure: false
})
export class ContestUserFilterPipe implements PipeTransform {

  transform(contests: Contest[], args?: any): any {
    return contests.filter(contest => contest.department.departmentName === args.department);
  }

}



