import { Component, OnInit, Input } from '@angular/core';

import { Contest, Student } from '../../_models';

import { ContestService, LoginService } from '../../_services';
import { CONTESTS } from '../../_datas';

@Component({
  selector: 'app-contest-list',
  templateUrl: './contest-list.component.html',
  styleUrls: ['./contest-list.component.css'],
  providers: [ContestService, LoginService]
})
export class ContestListComponent implements OnInit {
  @Input() user: any;
  contests: Contest[] = [];

  constructor(private contestService: ContestService,
    private loginService: LoginService) { }

  ngOnInit() {
    if (!this.user)
      this.user = this.loginService.getUser();
    this.getContests();
    // this.contests = CONTESTS;
  }

  getContests() {
    this.contestService.getContests().subscribe(
      contests => this.contests = contests
    );
  }
}



