import { DEPARTMENTS } from './departments';

export var single: Array<Object> = new Array();
for(var department of DEPARTMENTS){
  single.push({"name": department.departmentName, "value": getRandomIntInclusive(20,900)});
}
// single.push({"name": "计算机系", "value": 1});
//  = [
//   ,
//   {
//     "name": "2",
//     "value": 2
//   },
//   {
//     "name": "3",
//     "value": 3
//   }
// ]
function getRandomIntInclusive(min, max) {
  min = Math.ceil(min);
  max = Math.floor(max);
  return Math.floor(Math.random() * (max - min + 1)) + min;
}



