//教职工信息
export class Teacher {
  teacherno: number;//职工号  主键,非空
  teachername: string;//姓名
  gender: string;//性别
  birth: string;//出生日期
  department: string;//部门
  subject: string;//科室
  phone: string;//联系电话
  title: string;//职称
  post: string;//职务
  email: string;//电子邮箱
  type: string;//教职工类别
  direction: string;//教学研究方向
  education: string;//学历
  lab: string;//是否实验室人员
  password: string;//密码
  examiner: number;//是否审批人员

  constructor(teacherno: number, teachername: string, gender: string, birth: string, department: string,
    subject: string, phone: string, title: string, post: string, email: string, type: string,
    direction: string, education: string, lab: string, password: string, examiner: number) {
    this.teacherno = teacherno;
    this.teachername = teachername;
    this.gender = gender;
    this.birth = birth;
    this.department = department;
    this.subject = subject;
    this.phone = phone;
    this.title = title;
    this.post = post;
    this.email = email;
    this.type = type;
    this.direction = direction;
    this.education = education;
    this.lab = lab;
    this.password = password;
    this.examiner = examiner;
  }
}



