import { Component, OnInit, ElementRef, Input, ViewChild } from '@angular/core';
import { Http } from '@angular/http';

import { AdminService } from '../../../_services';

@Component({
  selector: 'app-admin-files',
  templateUrl: './admin-files.component.html',
  styleUrls: ['./admin-files.component.css'],
  providers: [AdminService]
})
export class AdminFilesComponent implements OnInit {
  @ViewChild('fileInput') inputEl: ElementRef;
  files;

  apply(file){
    let inputEl: HTMLInputElement = this.inputEl.nativeElement;
    let fileCount: number = inputEl.files.length;
    let formData = new FormData();
    if (fileCount > 0) { // a file was selected
      formData.append('file', inputEl.files.item(0));
      this.http.post("http://localhost:8080/CompSystem/admin/uploadFile", formData)
        .subscribe(() => alert('上传成功!'));
    }
  }

  getFiles(){
    this.http.get("http://localhost:8080/CompSystem/admin/allFile")
      .map(response => console.log("files:",response.json().filePage.list) || response.json().filePage.list)
      .subscribe(files => this.files = files);
  }

  delete(file,index){
    this.http.get("http://localhost:8080/CompSystem/admin/deleteFile" + "?fileId="+file.fileId)
      .subscribe(() => {
        this.files.splice(index,1);
        alert("删除成功!");
      });
  }
  constructor(private adminService: AdminService, private http: Http) { }

  ngOnInit() {
    this.getFiles();
  }

}



