import { Component, OnInit } from '@angular/core';
import { Http } from '@angular/http';

import { LoginService } from '../../../_services';

@Component({
  selector: 'app-student-message',
  templateUrl: './student-message.component.html',
  styleUrls: ['./student-message.component.css'],
  providers: [LoginService]
})
export class StudentMessageComponent implements OnInit {
  student;
  messages;

  getMessage(){
    this.http.get("http://localhost:8080/CompSystem/student/getAllMessage" + "?no=" + this.student.studentno)
      .map(response => console.log("StudentMessageComponent.getMessage:", response.json()) || response.json())
      .subscribe(response => this.messages = response);
  }

  constructor(private http: Http, private loginService: LoginService) { }

  ngOnInit() {
    this.student = this.loginService.getUser();
    this.getMessage();
  }

}



