import { Component, OnInit, Input } from '@angular/core';
import { Http } from '@angular/http';

import { LoginService, AwardService } from '../../_services';
import { Award,Student } from '../../_models';
import { AWARDS } from '../../_datas';

@Component({
  selector: 'app-award-list',
  templateUrl: './award-list.component.html',
  styleUrls: ['./award-list.component.css'],
  providers: [LoginService, AwardService]
})
export class AwardListComponent implements OnInit {
  @Input() user: any;
  student: Student;
  awards = [];

  download(award) {
    this.http.get("http://localhost:8080/CompSystem/student/downloadAwards")
      .map(response => console.log(response.json()) || response.json())
      .subscribe(data => {
        var blob = new Blob([data], { type: 'text/csv' });
        var url= window.URL.createObjectURL(blob);
        window.open(url);
      });
  }
  constructor(private loginService: LoginService,private awardService: AwardService,
    private http: Http) { }

  ngOnInit() {
    this.student = this.loginService.getUser();
    this.getAwards();
  }

  getAwards() {
    this.awardService.getAwards(this.student.studentno).subscribe(
      awards => this.awards = awards
    );
  }
}



