import { Contest } from './contest';
import { Teacher } from './teacher';

// 培训工作安排
export class Schedule {
  scheduleId: number;//培训工作id
  comid: number;//竞赛
  teacherno: number;//教师
  content: string;//培训内容
  position: string;//培训地点
  date: string;//培训日期
  hours: number;//学时

  constructor(
    scheduleId: number,
    comid: number,
    teacherno: number,
    content: string,
    position: string,
    date: string,
    hours: number) {
    this.scheduleId = scheduleId;
    this.comid = comid;
    this.teacherno = teacherno;
    this.content = content;
    this.position = position;
    this.date = date;
    this.hours = hours;
  }

  static newInstance(schedule: Schedule): Schedule {
    return new Schedule(
      schedule.scheduleId,
      schedule.comid,
      schedule.teacherno,
      schedule.content,
      schedule.position,
      schedule.date,
      schedule.hours
    )
  }
}



