import { Component, OnInit } from '@angular/core';
import { NgForm, FormControl } from '@angular/forms';
import { MdDialog, MdDialogRef } from '@angular/material/dialog';
import { Http } from '@angular/http';

import { ContestService } from '../_services';
import { Schedule, Budget, Hour, Teacher } from '../_models';
import { TEACHERS } from '../_datas';

@Component({
  selector: 'app-declaration',
  templateUrl: './declaration.component.html',
  styleUrls: ['./declaration.component.css'],
  providers: [ContestService]
})
export class DeclarationComponent implements OnInit {
  teachers;
  getTeachers() {
    this.http.get("http://localhost:8080/CompSystem/teacher/getAllTeacher")
      .map(response => console.log("DeclarationComponent.getTeachers:", response.json()) || response.json())
      .subscribe(response => this.teachers = response);
  }
  // TEACHERS: Teacher[];
  table = {
    schedule: [
      { prop: 'tno' },
      { name: 'content' },
      { name: 'place' },
      { name: 'date' },
      { name: 'hour' }
    ],
    budget: [
      { prop: 'subject' },
      { name: 'sum' },
      { name: 'reasons' }
    ],
    hour: [
      { prop: 'type' },
      { name: 'hour' },
      { name: 'remarks' }
    ]
  }
  schedules: Schedule[];
  budgets: Budget[];
  hours: Hour[];
  scheduleVisible = true;
  budgetVisible = true;
  hourVisible = true;
  title = '申报书';
  declaration = {
    baseInfo: {
      title: '基本信息',
      name: '竞赛名称',
      leader: '竞赛负责人',
      unit: '所在单位(盖章)',
      type: '竞赛类别',
      phone: '联系电话',
      email: '电子邮箱',
      date: '填表日期'
    },
    situation: {
      title: '竞赛情况',
      host: '院级以上竞赛举办者',
      time: '竞赛时间',
      place: '竞赛地点',
      object: '参赛对象',
      people: '参赛人数',
      sponsor: '校外资助单位',
      intro: '竞赛简介'
    },
    plan: {
      title: '实施方案',
      instructor: '指导教师',
      instructorNo: '教职工号',
      plan: '学科竞赛实施方案',
      schedule: {
        title: '培训工作安排',
        content: '培训内容',
        place: '培训地点',
        date: '培训日期',
        hour: '学时'
      }
    },
    goal: {
      title: '目标',
      tgoal: '能力训练目标',
      wgoal: '竞赛获奖目标'
    },
    budget: {
      title: '经费预算',
      scription: '经费预算(包括报名费、材料费、设备仪器费、交通差旅费等)',
      budget: {
        title: '经费预算',
        subject: '支出科目',
        sum: '金额(元)',
        reasons: '计算依据及理由'
      },
      hour: {
        title: '课时预算',
        type: '课时类别',
        hours: '课时数(学时)',
        remarks: '备注'
      }
    },
    opinion: {
      uopinion: '竞赛组织实施单位意见',
      tdopinion: '教学处意见'
    }
  };
  date: any;
  types = [
    { value: 'A类' },
    { value: 'B类' },
    { value: 'C类' }
  ];

  constructor(private contestService: ContestService, private http: Http) {
    // Object.assign(this, { TEACHERS });
  }

  ngOnInit() {
    this.schedules = [];
    this.budgets = [];
    this.hours = [];
    this.date = new Date().toLocaleDateString();
    this.getTeachers();
  }

  addSchedule(schedule: Schedule) {
    this.schedules.push(Schedule.newInstance(schedule));
  }
  deleteSchedule(schedule: Schedule) {
    var index = this.schedules.indexOf(schedule)
    if (index > -1) {
      this.schedules.splice(index, 1);
    }
    console.log(this.schedules);
  }

  addBudget(budget: Budget) {
    this.budgets.push(Budget.newInstance(budget));
  }
  deleteBudget(budget: Budget) {
    var index = this.budgets.indexOf(budget)
    if (index > -1) {
      this.budgets.splice(index, 1);
    }
    console.log(this.budgets);
  }

  addHour(hour: Hour) {
    this.hours.push(Hour.newInstance(hour));
  }
  deleteHour(hour: Hour) {
    var index = this.hours.indexOf(hour)
    if (index > -1) {
      this.hours.splice(index, 1);
    }
    console.log(this.hours);
  }

  toggleSchedule() {
    this.scheduleVisible = !this.scheduleVisible;
  }

  toggleBudget() {
    this.budgetVisible = !this.budgetVisible;
  }

  toggleHour() {
    this.hourVisible = !this.hourVisible;
  }

  submit(baseInfo, situation, plan) {
    baseInfo.date = new Date().toLocaleDateString();
    var competition = {
      comname: baseInfo.name,
      teacherno: baseInfo.leader,
      unit: baseInfo.unit,
      levelname: baseInfo.type,
      department: "",
      date: baseInfo.date,
      host: situation.host,
      time: situation.time,
      place: situation.place,
      introduce: situation.intro,
      object: situation.object,
      people: situation.people,
      sponsor: situation.sponsor,
      plan: plan.plan,
      tgoal: plan.tgoal,
      wgoal: plan.wgoal,
    }
    var declaration = {
      competition: competition,
      budgets: this.budgets,
      guideTeachers: [],
      hours: this.hours,
      schedules: this.schedules,
      compConditions: []
    }
    var gt = [];
    var cc = [];
    cc.push({conditionid:2});
    this.schedules.forEach(schedule => gt.push(new GuideTeacher(schedule.teacherno)));
    this.schedules.forEach(schedule => declaration.guideTeachers.push(new GuideTeacher(schedule.teacherno)));
    declaration.compConditions.push({conditionid:2});
    console.log("baseInfo:", baseInfo,
      "situation:", situation,
      "schedules:", this.schedules,
      "plan:", plan,
      "budgets:", this.budgets,
      "hours:", this.hours);
    console.log("declaration:",declaration);
    let formData = new FormData();
    formData.append("competition",JSON.stringify(competition));
    formData.append("budgets",JSON.stringify(this.budgets));
    formData.append("guideTeachers",JSON.stringify(gt));
    formData.append("hours",JSON.stringify(this.hours));
    formData.append("schedules",JSON.stringify(this.schedules));
    formData.append("compConditions",JSON.stringify(cc));
  console.log("competitionInfovo:",formData);
  // this.http.post("http://localhost:8080/CompSystem/teacher/addComp",formData)
  //   .subscribe(() => alert("提交成功!"));
    this.contestService.create(declaration).subscribe(response => alert(response));
  }
}

export class GuideTeacher {
  guideteaid: number;
  teacherno: number;
  comid: number;

  constructor(teacherno) {
    this.teacherno = teacherno;
  }
}



